<style>
  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
    min-height:1080px;
    padding: 10px;
  }

  .layout .ivu-select-single {
      height: 64px;
  }
  .layout .ivu-select-selection{
    border-radius: 0px;
    height: 64px;
  }
  .layout .ivu-select-single .ivu-select-selection .ivu-select-placeholder, .ivu-select-single .ivu-select-selection .ivu-select-selected-value{
    line-height: 64px;
    height: 64px;
  }
  .layout .ivu-layout-header{
    line-height: 0px;
  }
  .layout .layout-header-bar{
    padding: 0px;
  }
  .layout .ivu-input-wrapper{
    line-height: inherit;
  }
  .layout button{
    border-radius: 0px;
  }
  .layout input{
    border-radius: 0px;
  }
  .layout textarea{
    border-radius: 0px;
    min-height: 1080px;
  }
  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }
  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
  .content-css{
    min-height:1080px;
  }
  .content-css input{
    height: 100px;
  }
  .content-css button{
    height: 100px;
  }

  .left-css{
    min-height:1080px;
    width: 100%;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
    max-width: 100%;
    background: #495060;
  }
  .menu-css{
    min-height:1080px;
  }
  .layout-header-bar{
      background: #fff;
      box-shadow: 0 1px 1px rgba(0,0,0,.1);
      border-radius: 4px;
      height:64px;
  }
  .layout-content{
    margin-top: 20px;
  }
  .full-area textarea.ivu-input{
    min-height: 1080px;
  }
  .full-area textarea{
    min-height:1080px;
  }
  .full-area input{
    min-height:100px;
    height: 100px;
  }
  .input-out-css input{
    height:64px;
  }
  .test-css input{
    height: 100px;
  }
  .button-add{
    width: 100%;
    height: 64px;
  }
  .menuItem-css{
    display: flex;
    align-items: center;
    padding-right: 24px;
  }
  .menuItem-css button{
    border: 0px;
  }
  .test-css {
    width: 100%;
  }
  .test-css :active{
    font-size: 200px;
  }
  .layout div .ivu-select-selection{
    height: 64px;
  }

</style>
<template lang="jade">
  div.layout
    Layout
      Header.layout-header-bar
        // Input(v-model="name")
        //   Input(v-model="url",slot="prepend",style="width: 80px")
        //   Button(slot="append",icon="ios-search")
        Row(type="flex",justify="center",align="middle",class="code-row-bg")
          Col.input-out-css(span=4)
            Input(v-model="name", size="large", :readonly="!!status")
          Col.input-out-css(span=4)
            Select.select-div(:style="{height: '64px'}", v-model="select1", clearable)
              Option(v-for="method in methods",:key="method", :value="method") {{method}}
          Col.input-out-css(span=12)
            Input(v-model="url", size="large", :readonly="!!status")
          Col(span=4)
            Button.button-add(v-if="!!status", @click="updateItf", type="primary", icon="paper-airplane") 修改
            Button.button-add(v-else, @click="saveItf", type="success", icon="paper-airplane") 保存
      Layout.layout-content
        Row(type="flex",justify="center",align="middle",class="code-row-bg")
          Col(span=4)
            div.left-css
              Menu(theme="dark", width="auto")
                MenuItem(v-for="(myInterface) in myInterfaces", :style="{padding: '0px'}",
                :name="myInterface.uuid", :key="myInterface.uuid")
                  div.menuItem-css
                    span(:style="{flex:1,padding: '14px 24px'}", @click="viewItf(myInterface.uuid)") {{myInterface.name}}
                    span(:style="{textAlign: 'right'}", @click.stop="delItf(myInterface.uuid)")
                      Icon(type="close-circled", @click.stop="delItf(myInterface.uuid)")
                MenuItem(name="newItf",:style="{padding: '0px'}")
                    Button(type="info",icon="android-add", :style="{width: '100%', padding: '14px'}", @click="newItf") 添加
              // Button(@click="newItf") +新增
          Col(span=20)
            Layout.content-css
              Content(:style="{minHeight: '280px', background: '#fff'}")
                Input.full-area(v-model="mockData",type="textarea",:autosize="true", :readonly="!!status")
</template>
<script>
import {
  get, post, del, put
} from './utils/API.js';
import {
  mapActions,
  mapGetters
} from 'vuex';
import * as tools from './utils/index.js'
const MY_STATUS = {
  editable: 0,
  disable: 1
}
export default {
  data() {
    return {
      mockData: '',
      name: '',
      url: '',
      uuid: '',
      status : MY_STATUS.disable,
      select1: '',
      methods: ['get', 'put', 'post', 'delete']
    };
  },
  computed: {
    ...mapGetters(['myInterfaces'])
  },
  methods: {
    ...mapActions(['saveItfAction', 'removeItfAction']),
    newItf: function () {
        this.name = '';
        this.url = '';
        this.mockData = '';
        this.uuid = tools.guid();
        this.status = MY_STATUS.editable;
    },
    delItf: function(uuid) {
      console.log('delItf==>');
      this.removeItfAction(uuid);
      del('/operate', {uuid});
      this.state = MY_STATUS.disable;
      this.name='';
      this.uuid='';
      this.mockData='';
      this.url='';
    },
    saveItf: function() {
        const name = this.name;
        const url = this.url;
        const mockData = this.mockData;
        const uuid = this.uuid;
        const interfaceObj = {
          uuid,
          name,
          url,
          mockData
        }
        this.saveItfAction(interfaceObj);
        put('/operate', {uuid}, interfaceObj);
        // post('/operate', interfaceObj);
        this.status = MY_STATUS.disable;
    },
    updateItf: function () {
      if(!!this.uuid) {
        this.status = !!MY_STATUS.disable ? MY_STATUS.editable:MY_STATUS.disable;
      } else {
        // this.
      }
    },
    viewItf: function(itf) {
        this.status = MY_STATUS.disable;
        const {
          name,
          url,
          mockData,
          uuid
          } = this.myInterfaces[itf] || {};

        this.name = name;
        this.url = url;
        this.mockData = mockData;
        this.uuid = uuid;
        this.status = MY_STATUS.disable;
        console.log('view==>');
        get('/operate', {uuid: this.uuid});
    }
  }
};

</script>



