import axios from 'axios';

// const baseUrl = `http://localhost:3000`;
const baseUrl = ``;
const getUrl = (url) => `${baseUrl}${url}`;

export const get = async (url, query) => {
    const result = await axios.get(`${getUrl(url)}?`, {
        params: query
    });
    return result;
}

export const post = async (url, body) => {
    // const result = await axios.post(getUrl(url),
    //     body
    //     , {headers: {
    //         // 'Content-Type': 'application/json',
    //         'Content-Type': 'application/x-www-form-urlencoded',
    //         'Content-Encoding': 'identity',        
    //     }}
    // );
    const result =await axios({
        method: 'post',
        url:getUrl(url),
        data: body,
        transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
    return result;
}

export const put = async (url, query, body) => {
    // const result = await axios.put(getUrl(url), {
    //     params: query,
    //     data: body
    // })
    const result =await axios({
        method: 'put',
        url:getUrl(url),
        params: query,
        data: body,
        transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
    return result;
}

export const del = async (url, query) => {
    const result = await axios.delete(getUrl(url), {
        params: query
    })
    return result;
}


