import Vue from 'vue';
import Vuex from 'vuex';
// import count from './count';
import interModules from './modules/Interface';

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
  strict: debug,
  modules: {
    interModules,
  },
//   plugins: debug ? [createLogger()] : []
});



