import { stat } from "fs";

const state = {
    interfaces : {'e4eca12f-c80e-4da5-bc49-72938fd2c980':{
        uuid: 'e4eca12f-c80e-4da5-bc49-72938fd2c980',
        name: 'test',
        url: '/getList',
        mockData: ``
    }, 'a61758e1-c0ad-4c0a-8608-ed7767eb4d8e': {
        uuid: 'a61758e1-c0ad-4c0a-8608-ed7767eb4d8e',
        name: 'test2',
        url: '/getInfoById',
        mockData: ``
    }}
}

const getters = {
    myInterfaces : state => state.interfaces
}

const actions = {
    saveItfAction: ({state, commit}, interfaceObj) => {
        const {
            name, 
            url,
            uuid
        } = interfaceObj;
        if(!!uuid && !!name && !!url) {
            commit('add', interfaceObj)
        }
    },
    removeItfAction: ({state, commit}, uuid) => {
        commit('remove', uuid);
    }
}

const mutations = {
    add: (state, interfaceObj) => {
        // state.interfaces[interfaceObj.url] =interfaceObj ;
        state.interfaces ={...state.interfaces, [interfaceObj.uuid]:interfaceObj} ;
        console.log(state.interfaces);
        // console.log('fuck==>')
    },
    remove: (state, uuid) => {
        delete state.interfaces[uuid];
        state.interfaces = {...state.interfaces};
    }
}

export default {
    state,
    mutations,
    getters,
    actions
}


